<template>
	<div>
		<template v-if="loadingJob">
			<div class="div__loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row user-profile">
				<div class="user-profile__card">
					<div class="user-profile__primary">
						<p class="user-profile__name">{{ itemData.title }}</p>
					</div>

					<div class="user-profile__section">
						<h5 class="user-profile__heading">Job Information</h5>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Location:</p>
							<p>{{ itemData.location }}</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Type:</p>
							<p>{{ itemData.type }}</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Closing Date:</p>
							<p>{{ itemData.closing_date | date }}</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Status:</p>
							<p>
								<span :class="isActive">
									{{ itemData.active === 1 ? 'Active' : 'Suspended'}}
								</span>
							</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Description:</p>
							<p v-html="itemData.description"></p>
						</div>
						<div v-if="getLoggedUser.category === 'administrator'">
							<button @click="deleteJob" class="btn btn__danger">Delete</button>
							<Loader v-if="deletingJob" />
						</div>
					</div>
				</div>

				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="applicant-tab" data-toggle="tab" href="#applicant" role="tab" aria-controls="applicant" aria-selected="true">Applicants</a>
						</li>
						<li class="nav-item">
							<a class="nav-link" id="activity-tab" data-toggle="tab" href="#activity" role="tab" aria-controls="activity" aria-selected="false">Edit Job</a>
						</li>
					</ul>
					<div class="tab-content" id="myTabContent">
						<div class="tab-pane fade show active user-profile__tabs__section" id="applicant" role="tabpanel" aria-labelledby="applicant-tab">
							<div class="user-profile__notification">
								<div class="activity">					
									<div>
										<section class="main-grid__row contacts contacts--list">
											<div class="contacts--list__wrapper">
												<div class="contacts--list__item head">
													<div></div>
													<p>Name</p>
													<p>Email</p>
													<p>Resume</p>
													<p>Action</p>
												</div>
												<div class="contacts__body">
													<template v-if="applicants.length">
														<div 
															v-for="applicant in applicants"
															:key="applicant.id"
															:applicant="applicant"
															class="contacts--list__item">
															<label class="check">
																<input type="checkbox">
																<div class="check-mark"></div>
															</label>
															<p class="info__email">
																<router-link
																	:to="{ name: 'user-profile', params: { id: applicant.user.id }}"
																>
																	{{ applicant.user.fname }} {{ applicant.user.lname }}
																</router-link>
															</p>		
															<p>{{ applicant.user.email }}</p>
															<button class="btn btn outline" type="button">
																<a v-bind:href="applicant.resume" target="_blank">Resume</a>
															</button>
															<div>
																<template v-if="applicant.status === 'employed'">
																	<button
																	@click="changeStatus(applicant.user.id, 'accepted')"
																	class="btn btn__outline"
																	type="button">
																	Accept
																	</button>
																	<button
																		class="btn btn__danger"
																		@click="changeStatus(applicant.user.id, 'rejected')"
																		type="button">
																		Reject
																	</button>
																</template>
																<template v-else-if="applicant.status === 'accepted'">
																	<button
																		class="btn btn__save"
																		@click="changeStatus(applicant.user.id, 'employed')"
																		type="button">
																		Employ
																	</button>
																	<button
																		class="btn btn__danger"
																		@click="changeStatus(applicant.user.id, 'rejected')"
																		type="button">
																		Reject
																	</button>
																</template>
																<template v-else-if="applicant.status === 'rejected'">
																	
																</template>
																<template v-else>
																	<button
																		class="btn btn__save"
																		@click="changeStatus(applicant.user.id, 'employed')"
																		type="button">
																		Employ
																	</button>
																	<button
																		@click="changeStatus(applicant.user.id, 'accepted')"
																		class="btn btn__outline"
																		type="button">
																		Accept
																	</button>
																	<button
																		class="btn btn__danger"
																		@click="changeStatus(applicant.user.id, 'rejected')"
																		type="button">
																		Reject
																	</button>
																</template>
															</div>
														</div>
													</template>
													<template v-else>
														<p>No job applicant yet</p>
													</template>
												</div>
											</div>
											<Pagination
												:options="{ limit, offset, total, baseUrl }"
												v-if="total > limit"
											/>
										</section>
									</div>
								</div>
							</div>
						</div>
						<div class="tab-pane fade user-profile__tabs__section" id="activity" role="tabpanel" aria-labelledby="activity-tab">
							<div class="settings">
								<form
									method="post"
									@submit.prevent="updateJob">
									<div class="settings__primary">
										<button
											@click.prevent="toggleJobStatus"
											class="btn"
											:class="isSuspend"
											v-show="!isSelf"
											type="button"
											data-dismiss="modal"
											aria-label="close"
											name="button">
											{{ itemData.active === 1 ?
												'Suspend Job' :
												'Activate Job'
											}}
										</button>
										<button
											class="btn btn__save"
											type="button"
											name="button"
											@click.prevent="updateJob"
											v-bind="{'disabled': loading}">
											Save
										</button>
									</div>
									<div class="settings__field">
										<div class="form-row">
											<div class="form-group col-md-12">
												<div class="settings__value">
													<label for="jobTitle">Title</label>
													<input
														:class="[{'is-invalid': errors.title}]"
														@keydown="clearErrors($event.target.name)"
														v-model="itemData.title"
														name="title"
														value=""
														type="text"
														class="form-control"
														id="jobTitle">
												</div>
												<div class="invalid-feedback">
													{{ errors.title }}
												</div>
											</div>
										</div>
										<div class="form-row">
											<div class="form-group col-md-12">
												<div class="settings__value">
													<label for="jobType">Type</label>
													<form class="form__select">
														<select
															@change="updateJob"
															v-model="jobType"
															class="custom-select" name="">
															<option selected value="">Change type</option>
															<option value="full-time">Full Time</option>
															<option value="contract">Contract</option>
														</select>
													</form>
												</div>
											</div>
										</div>
										
										<div class="form-row">
											<div class="form-group col-md-12">
												<div class="settings__value">
													<label for="jobLocation">Location</label>
													<form class="form__select">
														<select 
															class="form-control"  
															id="career"
															:class="[{'is-invalid': errors.location}]"
															v-model="jobLocation"
															@change="updateJob">
															<option selected disabled>Select a location for the job</option>
															<option 
																v-for="(state, i) in states"
																:key="i"
																:value="state.value">{{state.text}}</option>
														</select>
													</form>
													<div class="invalid-feedback">
														{{ errors.location }}
													</div>
												</div>
											</div>
										</div>

										<div class="form-row">
											<div class="form-group col-md-6">
												<label class="modal__label" for="dob">Closing Date</label>
												<date-picker v-model="itemData.closing_date" :config="options">
												</date-picker>
												<span class="modal__form__icon">
													<svg width="24" height="24" fill="none" xmlns="http://www.w3.org/2000/svg">
													<title>DOB Icon</title>
													<path fill="none" d="M0 0h24v24H0z"/>
													<path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5a2.5 2.5 0 0 1 0-5 2.5 2.5 0 0 1 0 5z" fill="#828282"/>
													</svg>
												</span>
												<div class="invalid-feedback">
													{{ errors.closing_date }}
												</div>
											</div>
										</div>
										<div class="form-row">
											<div class="form-group">
												<label class="modal__label" for="dob">Description</label>
												<quill-editor ref="myTextEditor"
													v-model="itemData.description"
													:options="editorOption">
												</quill-editor>
											</div>
										</div>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import datePicker from 'vue-bootstrap-datetimepicker';
import '@fortawesome/fontawesome-free/css/fontawesome.css';
import '@fortawesome/fontawesome-free/css/regular.css';
import '@fortawesome/fontawesome-free/css/solid.css';
import 'pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css';
import { mapGetters } from 'vuex';
import {
	getJob, updateJob, getApplicants, changeStatus, deleteJob 
} from '../../utils/api/jobs';
import Loader from '../../components/Loader';
import job from '../../utils/validations/job';
import form from '../../mixins/form';
import { success, fail } from '../../utils/toast';
import Pagination from '../../components/Pagination';
import states from './locations';
import eventHub from '../../utils/eventhub';

export default {
	name: 'JobDetail',
	mixins: [form],
	data() {
		return {
			itemData: {},
			loadingJob: true,
			jobLoaded: false,
			errors: {},
			jobType: '',
			options: {
				format: 'YYYY-MM-DD',
				useCurrent: true
			},
			applicants: [],
			applicantsLoading: true,
			applicantsLoaded: false,
			limit: 6,
			offset: 0,
			total: 0,
			baseUrl: '',
			states,
			deletingJob: false,
			jobLocation: '',
			jobResult: '',
			locationResult: '',
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getApplicants();
		}
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.baseUrl = `jobs/${this.$route.params.id}/detail`;
		this.getJob();
		this.getApplicants();
		eventHub.$on('applicant-status-updated', (payload) => {
			this.applicants.map((item) => {
				if (item.id === payload.id) {
					item.status = payload.status;
					return true;
				}
				return false;
			})
		})
	},
	methods: { 
		deleteJob() {
			this.deletingJob = true;
			deleteJob(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.deletingJob = false;
						success({
							title: 'Success! Job has been deleted!'
						});
						setTimeout(() => {
							this.$router.push('/jobs');
						}, 500);
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.deletingJob = false;
					fail({
						title: 'Oops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		getJob() {
			getJob(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						const result = response.data.data;
						delete result.created_at;
						delete result.updated_at;
						this.itemData = result;
						this.jobResult = this.itemData.type;
						this.locationResult = this.itemData.location;
						return true;
					}
					this.jobLoaded = false;
					return false;
				}).catch((error) => {
					this.jobLoaded = false;
					return error;
				}).finally(() => {
					this.loadingJob = false;
				});
		},
		updateJob() {
			/**
			 * Validate input
			 */
			const result = this.itemData;
			result.type = this.jobType || this.jobResult;
			result.location = this.jobLocation || this.locationResult;
			delete result.user_id;
			this.validate(result, job.update);
			if (Object.keys(this.issues).length > 0) {
				this.errors = this.issues;
				return false;
			}

			updateJob(result)
				.then((response) => {
					if (response.status === 200) {
						success({
							title: 'Success! Job has been updated!'
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		toggleJobStatus() {
			this.itemData.active = this.itemData.active === 0 ? 1 : 0;
			updateJob(this.itemData)
				.then((response) => {
					if (response.status === 200) {
						const message = this.itemData.active === 1
							? 'Job has been activated'
							: 'Job has been suspended';
						success({
							title: `Success! ${message}` 
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		getApplicants() {
			getApplicants(this.$route.params.id, this.limit, this.offset)
				.then((response) => {
					if (response.status === 200) {
						this.applicants = response.data.data;
						this.total = response.data.total;
						this.applicantsLoaded = true;
						return true;
					}
					this.applicantsLoaded = false;
					return false;
				})
				.catch((error) => {
					this.applicantsLoaded = false;
					return error;
				})
				.finally(() => {
					this.applicantsLoading = false;
				});
		},
		changeStatus(id, value) {
			const data = {
				user_id: id,
				status: value
			};
			changeStatus(this.$route.params.id, data)
				.then((response) => {
					if (response.status === 200) {
						eventHub.$emit('applicant-status-updated', response.data);
						success({
							title: `Success! Applicant has been ${data.status}!`
						});
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	computed: {
		...mapGetters([
			'getLoggedUser'
		]),
		isSuspend() {
			return [
				this.itemData.active === 1
					? 'btn__danger'
					: 'btn__save'
			];
		},
		isActive() {
			return [
				this.itemData.active === 1
					? 'badge badge-pill badge-success'
					: 'badge badge-pill badge-danger'
			];
		},
		isSelf() {
			return this.itemData.id === this.$session.get('id');
		},
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	},
	components: {
		Loader,
		datePicker,
		Pagination,
		quillEditor
	},
	filters: {
		date(value) {
			return moment(value).format('Do, MMM YYYY');
		}
	}
};
</script>

